.container{
    padding-top: 20px;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 937px ){
    .container{
        justify-content: center;
    }
}