@import "../../common/styles/sass/variables";

.title{
    position: relative;
    margin-bottom: 30px;
    & a{
        text-decoration: none;
        color: inherit;
    }
    & h2{
        width: fit-content;
        max-width: 100%;
        margin: 0;
        padding: 0;
        font-size: 40px;
        font-weight: 600;
        letter-spacing: 0;
        line-height: 1;
        background: linear-gradient(to right,var(--mc1),var(--mc2));
        -webkit-background-clip: text;
        -webkit-text-stroke: 4px $bgSecondaryColor;
        filter: drop-shadow(0 5px 20px rgba(204,0,255,.15));



    }
    &::after{
        position: absolute;
        content: "";
        width: 100%;
        height: 2px;
        background-color: #ddd;
        bottom: -10px;
        transform: translate(-50%,50%);
        left: 50%;
    }
}

